@use "base/variables" as v;
@use "base/mixins" as m;

.navegacion{
    display: flex;
    flex-direction: column;

    @include m.desktop{
        flex-direction: row;
    }

    &__enlace{
        color: v.$blanco;
        font-size: 2.2rem;
        padding: 2rem;
        font-weight: v.$bold;
        text-transform: uppercase;
        text-align: center;

        &:hover{
            background-color: white;
            color: v.$azul;
        }
    }


}